

.App-header {
    margin: 40px 340px;
    padding-block: 10px;
    background-color: #282c34;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  .img{
    width: 88px;
    height: 88px;
    border-radius: 100%;
    justify-self: center;
    margin-left: 30px;
  }
  
  .links{
    display: grid;
    grid-template:  40px / 2fr;
    gap: 30px;
  }
  
  .btn{
    font-size: 18px;
    height: 40px;
    padding-inline: 40px;
    color: #000;
    text-align: center;
    align-items: center;
    background-color: rgb(202, 203, 206);
    border: 1px solid rgb(143, 141, 141);
    border-radius: 5px;
    max-width: 1152px;
    text-decoration: none;
  }
  .btn--4{
    font-size: 18px;
    height: 40px;
    padding-inline: 40px;
    color: #000;
    text-align: center;
    align-items: center;
    background-color: rgb(128, 129, 131);
    border: 1px solid rgb(143, 141, 141);
    border-radius: 5px;
    max-width: 1152px;
    text-decoration: none;
  }
  
  .btn:hover{
    background-color: rgb(240, 240, 240);
  }
  .btn--4:hover{
    background-color: rgb(240, 240, 240);
  }
  
  .icon{
    margin-top: 20px;
  }
  .icon > img{
    margin: 10px;
  }